<template>
      <div style=" clear: both;text-align: left;">
          <el-form ref="form" :model="form" :inline="true">
           
            <el-row :gutter="12">
              <el-col :span="8">
                   <el-form-item label="">
              <el-input v-model="form.workname" placeholder=""> </el-input>
            </el-form-item>
              </el-col>

              <el-col :span="8">
                <el-form-item label="">
                    <el-input v-model="form.author" placeholder=""> </el-input>
                </el-form-item>
              </el-col>

              <el-col :span="8">
                  <el-form-item label="">
                    <el-input v-model="form.author" placeholder=""> </el-input>
                  </el-form-item>
              </el-col>

            </el-row>

            <el-row>
              <el-col :span="8">
                <el-form-item label="">
                    <el-input v-model="form.workname" placeholder=""> </el-input>
                </el-form-item>

              </el-col>
              <el-col :span="8">
                  <el-form-item> 
                    <el-button type="primary" @click="onSubmit()"></el-button>
                  </el-form-item> 
              </el-col>
            </el-row>
            
             
            
          </el-form>
          <!---->
          <div class="buttonenum">
             <el-button size="medium" type="primary" @click="fenpeiwork()"></el-button>
             <el-button size="medium"></el-button>
             <el-button size="medium">excel</el-button>
             <div>
               ------------{{id1}},{{id2}},{{id3}},{{id4}}-------
             </div>
          </div>
          <div style="margin-top:16px;">
            <template>
              <!--       -->
              <el-table :data="tableData" stripe style="width: 100%">
                <el-table-column
                  label=""
                  type="index"
                   width="116"
                  align="center">
                </el-table-column>
                <!-- name author address shcoolname private -->
                <el-table-column
                  prop="works.title"
                  label=""
                  width="116">
                </el-table-column>
                <el-table-column
                  prop="author_"
                  label=""
                  width="116">
                </el-table-column>
                <el-table-column
                  prop="mentor_"
                  label=""
                  width="116">
                </el-table-column>
                <el-table-column
                  prop="areaName"
                  label=""
                  width="116">
                </el-table-column>
                <el-table-column
                  prop="title_"
                  label=""
                  width="116">
                </el-table-column>
                 <el-table-column
                  prop="info.category"
                  label=""
                  width="116">
                </el-table-column>
                 <el-table-column
                  prop="info.project"
                  label=""
                  width="116">
                </el-table-column>
                <el-table-column
                  prop="private"
                  label=""
                  width="116">
                </el-table-column>
                
                <!-- <el-table-column
                  prop="address"
                  label=""
                  width="180">>
                </el-table-column>
                <el-table-column
                  prop="shcoolname"
                  label=""
                  width="180">
                </el-table-column>
                <el-table-column
                  prop="private"
                  label=""
                  width="180">
                </el-table-column> -->
              </el-table>
            </template>
          </div>
           <div class="block">
              
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[perPage]"
                :page-size="perPage"
                layout="total, sizes, prev, pager, next, jumper"
                :total="totalCount">
              </el-pagination>
            </div>
             
                  <div v-if="dialogVisible" class="mask">
                    <div class="box">
                      <my-transfer @edit="edit" @Noedit="Noedit"></my-transfer>
                    </div>
                  </div>
                  
                   <!-- totalCount:'',
          pageCount: "",
          currentPage: "",
          perPage: "",
               -->
     
      </div>
      
    
</template>
<script>
import MyTransfer from '../../components/MyTransfer';
import api from '../../service/api';
import { axiosGet } from '../../tools/tools';

  export default {
    components:{MyTransfer},
     
    props:['id1',"id2","id3","id4"],
    // props:{
    //     id1:Number,
    //     id2:Number,
    //     id3:Number,
    //     id4:String,
    // },
    data() {
        const generateData = _ => {
        const data = [];
       
        const cities = ['', '', '', '', '', '', '','',""];
        const pinyin = ['', '', '', '', '', '', '','',""];
       
        cities.forEach((city, index) => {
          data.push({
            label: city,
            key: index,
            pinyin: pinyin[index]
          });
        });
        return data;
      };
      return {
       
            data: generateData(),
            value: [],
            filterMethod(query, item) {
              return item.pinyin.indexOf(query) > -1;
            },
            form:{
              addr:'',
              schoolname:'',
              workname:'',
              phone:'',
              author:'',
            
            },
            AddrList:[
                {id:1,name:""},
                {id:2,name:""},
                {id:3,name:""},
            ],
            SchoolList:[
                {id:1,name:""},
                {id:2,name:""},
                {id:3,name:""},
            ],
            //     
            tableData:[],
            
              
              dialogVisible:false,

          totalCount:'',
          pageCount: "",
          currentPage: 1,
          perPage: "",
            
      };
    },
    watch:{
        id1:function(val){
        console.log(val,"watch1")
        },
        id2:function(val){
            console.log(val,"watch2")
        },
        id3:function(val){
            console.log(val,"watch3")
        },
        id4:function(val){
            console.log(val,"watch4")
        }
    },
    created(){
      this.enrollSelect()

    },
    mounted(){
      
      
    },
    methods: {
      
     async enrollSelect(){

        
          let parmas={}
          parmas.url=api.enroll
          parmas.expand='info,works,school'
          parmas.page=this.currentPage
          parmas.activity_id=sessionStorage.getItem("workid")
          let res=await axiosGet(parmas).catch(err=>err)
         
          this.tableData=res.items
         
          this.tableData.forEach(ite=>{
             var title=""
             var areaName=""
             var author_=""
             var mentor_=""
            ite.school.forEach(item=>{
           title==""?title=item.title:title=title+","+item.title
            areaName=item.areaInfo.areaName
          })
          ite.works.member.author.forEach(item=>{
            author_==""?author_=item.name:author_=author_+","+item.name
          })
          ite.works.member.mentor.forEach(item=>{
            mentor_==""?mentor_=item.name:mentor=mentor_+","+item.name
          })
          ite.mentor_=mentor_
          ite.author_=author_
          ite.areaName=areaName
          ite.title_=title
          })
          // totalCount: 22
          // pageCount: 2
          // currentPage: 1
          // perPage: 20
          this.totalCount=res._meta.totalCount
          this.pageCount=res._meta.pageCount
          this.currentPage=res._meta.currentPage
          this.perPage=res._meta.perPage
         
         
           console.log(this.tableData)
      },
      edit(item){
        this.dialogVisible=item;
      },
      Noedit(item){
        this.dialogVisible=item;
      },
        handleClose(done) {
          this.$confirm('')
            .then(_ => {
              done();
            })
            .catch(_ => {});
        },
      
      fenpeiwork(){
        if(this.dialogVisible==true){
           this.dialogVisible=false;
        }else{
          this.dialogVisible=true;
        }
       
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        this.currentPage=val
        console.log(`: ${val}`);
        this.enrollSelect()
      }
      
    }
    
  };
</script>
<style lang="less" scoped>
  .secendROw{
        margin-left: -126px;
  }
  .block{
    margin-top:16px;
    margin-left:276px;
  }
  .buttonenum{
    float:left;
    margin:20px 0 10px 0;
  }
  .mask{
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
  z-index: 3;
  }
  .box{
    background: #ffffff;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-top: -250px;
    margin-left: -400px;
    width:782px;
    height:500px;
    background:rgba(255,255,255,1);
    border-radius:6px;
  }

</style>
