<template>

    <div class="outter">
        <el-card style="min-height:650px">
            <div style="position:relative">
                <el-tabs v-model="activeName" @tab-click="handleClick">
                    <el-tab-pane label="" name="first">
                      
                            <el-form ref="form" :inline="true" :model="form" class="demo-form-inline">
                                
                                        <el-form-item label="">
                                                <el-date-picker format="yyyy-MM-dd" v-model="form.time" size="small" type="daterange" range-separator="" start-placeholder="" end-placeholder="">   
                                                </el-date-picker>
                                        </el-form-item>
                                          
                                        <el-form-item label="">
                                            
                                            <el-input v-model="form.acitvename" placeholder="" size="small"> </el-input>
                                            
                                        </el-form-item>
                                   
                                        <el-form-item >
                                            <el-button  type="primary" @click="onSubmit()" size="small"></el-button>
                                        </el-form-item>
                               
                            </el-form>

                        
                        <div v-for='(item,index) in list' :key="index">
                            <div class="count" >
                                <div class="sc">
                                    <div class='imglogo'>
                                        <img :src="item.cover" style="width:173px;height:127px;border-radius:8px;"/>
                                    </div>
                                    <div class="activeclass">
                                        <p class="font1">{{item.title}}</p>
                                        <p  >
                                            {{item.targetList}}
                                        </p>
                                        <p >{{item.regionList}} </p>
                                        <p >{{item.Sta_created_at}} </p>
                                    </div>
                                </div>
                                <div class="shenheclass" >
                                    <!-- <p>{{item.progress}}</p> -->
                                   <p>haol</p>
                                </div>
                                <div class="shenheclass2" >
        
                                    <el-button type="text" @click="goToManagerCenter2(item.id)"  style="display:block;margin-left:9px;"></el-button>
                                    <el-button type="text" @click="goToManagerCenter(item.id)"  style="display:block">></el-button>
                                    <el-button type="text" style="display:block;margin-top:20px;color:red;" @click="deleteOne(item.id)"></el-button>
                                </div>
                            </div>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="" name="second">

                    </el-tab-pane>
                </el-tabs>
                <el-button size='mini' type="primary" style='position: absolute;right:0px;top:0px;' @click="fabuActive()">+</el-button>
            </div>
        </el-card>
    </div>
    
</template>
<script>
import axios from 'axios';
import api from '../../service/api';
import { timestampToTime, axiosDelete, getCookie } from '../../tools/tools';
import Axios from 'axios';
export default {
    data(){
        return{
            list:[],
            activeName: 'first',
            form:{
                object:'',
                limit:'',
                time:'',
                acitvename:'',
            },
            AOList:[
                {id:1,name:"1"},
                {id:2,name:"2"},
                {id:3,name:"3"},
            ],
             AlList:[
                {id:1,name:"4"},
                {id:2,name:"5"},
                {id:3,name:"6"},
            ],
             ClassList:[
                {name:"",id:1},
                {name:"",id:2},
                {name:"",id:4},
                {name:"",id:8},
                {name:"",id:16},
                {name:"",id:32},
                {name:"",id:64},
                ],
            
            // list:[
            //     {
            //         id:1,
            //         name:"",
            //         object:"",
            //         limit:"",
            //         time:"2020-01-20",
            //         url:require('../../assets/img2/1.png'),
            //         status:""
            //         },
            //     {
            //         id:2,
            //         name:"",
            //         object:"",
            //         limit:"",
            //         time:"2020-01-20",
            //         url:require('../../assets/img2/1.png'),
            //         status:"",
            //         },
               
            // ]
            userid:"",//id
        }
    },
    created(){
        this.userid=JSON.parse(localStorage.getItem("user")).id
        this.selectActive();
    },
     methods: {
    //
        deleteOne(id){
            let params={}
            params.url=api.activity;
            params.id=id;
             axios.delete(params.url+"/"+params.id, {
                headers: {
                    'x-api-key':getCookie("x-api-key")
                }
                }).then(res => {
                    console.log(res)
                if(res.status==200){
                        
                    this.$message({
                        type: 'success ',
                        message:""
                    });
                }
                 if(res.status==204){
                        
                    this.$message({
                        type: 'success ',
                        message:""
                    });
                }
               this.selectActive();
                }).catch(err => {
                    
                    console.log(err,"111111111",err.code)
                     
                     this.$message({
                        type: 'warning ',
                        message:""
                    });
               
            });
        },

      handleClick(tab, event) {
        console.log(tab, event);
      },
    
         
    async selectActive(){
        console.log(1)
            let params={}
            params.url=api.activityDetail,
            params.expand="detail,region,node,attachment,banner,category,categoryDetail,process"
            let res = await this.axiosGet(params).catch(err => err);
            this.list=res.items
            this.list.forEach(item=>{
                var Str="";//
                console.log(item.target,"item.target")
                var ii=item.target.toString(2)
                for(let i=0;i<ii.length;i++){
                  if(ii[i]==1){
                      Str=Str+''+this.ClassList[i].name+''
                  }
                }
                item.targetList=Str //
                
                var Str2=""//
                for(let i=0;i<item.region.length;i++){
                    Str2=Str2+''+item.region[i].area_name+''
                }
                item.regionList=Str2

                var Str3=""//
                let opts={}
                opts.dateZero="dateZero"
                Str3=this.timestampToTime(item.created_at,opts)
                item.Sta_created_at=Str3
            })
    },

      onSubmit(){

      },
      fabuActive(){
          this.$router.push({
              path:"/activeManager/createActivity",
          })
          
      },
    goToManagerCenter2(item){
          this.$router.push({
                path: '/home/activityIntroduction',
                query: {
                id: item
                }
            });
    },
    goToManagerCenter(item){
        
         this.$router.push({
              path:"/activeManager/managerCenter",
              query: {
                    name: 'third',
                    id: item,
                }
          })
        
    }
    },

 
}
</script>
