.sc{
    width: 640px;;
}
    .outter{
        width:1180px;
        margin:auto;
        margin-top:20px;
    }

    .count{
        display:flex;
        width:1120px;
        height:170px;
        background:rgba(247,248,250,1);
        border:1px solid rgba(229,229,229,1);
        border-radius:8px;
        margin-bottom:20px;
    }
    .imglogo{
        float:left;
        margin:20px 0 0 20px;
    }
  
    .font2{
         /* margin-top:20px; */
        float:left;
        
    }
    .shenheclass{
        margin-left:100px;
        width:150px;
        display:flex;
        justify-content:space-between;
        align-items:center;
        font-size:14px;
        font-weight:400;
        color:rgba(255,198,44,1);
    }
    .shenheclass2{
            margin: 20px 160px 0 0;
    }
    .activeclass{
        
        display:flex;
        flex-direction: column;
        justify-content: start;
        p{
            padding: 5px 10px 4px 61px;
            font-size:14px;
            font-family:Microsoft YaHei;
            font-weight:400;
            color:rgba(102,102,102,1);
            line-height:24px;
        }
        .font1{
            margin:7px 0 0 0;
            float:left;
            font-size:24px;
            font-family:Microsoft YaHei;
            font-weight:400;
            color:rgba(51,51,51,1);
            line-height:43px;
        }
    }
 .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }



    
</style>